/* src/App.css */
.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  align-items: left;
  justify-content: left;
  background-color: black; /* Set black background */
  color: white; /* Set text color to white */
  overflow:hidden;
  scrollbar-width: 0;
}

.title {
  text-indent: 75%;
  color: white;
}

.video-container {
  position: absolute;
  display: flex;
  background-color: white;
}
.video-wrapper {
  z-index: 1;
  display: block;
  width: 30vw;
  position: relative;
  resize: both; /* Enables resizing */
  transition: transform 0.1s ease;
  overflow: visible;

}

.video-wrapper:nth-child(1) {position: absolute; top: 0; left: 0; }
.video-wrapper:nth-child(2) {position: absolute; top: 33vh; left: 36vw; }
.video-wrapper:nth-child(3) {position: absolute; bottom: -100vh; right: -99vw; }


.video-wrapper:nth-child(1).enlarge {
  transform: scale(3) translateX(11.5vw) translateY(12vh);
  z-index: 2;
}
.video-wrapper:nth-child(2).enlarge {
  transform: scale(3);
  z-index: 2;
}
.video-wrapper:nth-child(3).enlarge {
  transform: scale(3) translateX(-11.5vw) translateY(-12vh);
  z-index: 2;
}

.video-wrapper:nth-child(1).hovered {
  transform: scale(1.5) translateX(6vw) translateY(8vh);
  z-index: 2;
}
.video-wrapper:nth-child(2).hovered {
  transform: scale(1.5);
  z-index: 2;
}
.video-wrapper:nth-child(3).hovered {
  transform: scale(1.5) translateX(-8vw) translateY(-8vh);
  z-index: 2;
}


.video-player {
  width: 100%;
  height: auto;
}


.buttoncontainer {
  margin-top: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100vw;
  padding: 2vw;
}


.audio-mixers {
  display: flex;
  height: fit-content;
  width: 100%;
  height: 500%;
  margin-bottom: 7%;
  margin-left: -5%;
  flex-direction: row;

}

.audio-mixer {
  margin-right: -8%;
  transform: rotate(-90deg);
}


.playbar {
  position: relative;
  display: flex;
  flex-direction: row;
  align-content: center;
}

.play-button {
  width: fit-content;
  height: fit-content;
  cursor: pointer;
  color: white;
  background-color: black;
  border-style: none;
}

.seek-slider {
  -webkit-appearance: none;
  width: 60vw;
  height: 2vh;
  border-radius: 20px;  
  background: white;
  opacity: 1;
  -webkit-transition: .2s;
  transition: opacity .2s;
  margin: 1.5%;
}